<template>
    <div>
        this is other
        <Button>Default</Button>
        <Button type="primary" ghost>Primary</Button>
        <Button type="dashed" ghost>Dashed</Button>
        <Button type="text" ghost> Text</Button>
        <br><br>
        <Button type="info">Info</Button>
        <Button type="success">Success</Button>
        <Button type="warning">Warning</Button>
        <Button type="error">Error</Button>

        <div>
            <Button type="info" v-on:click="login">Info</Button>
            <img v-bind:src="base64" >
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name: "Other",
        data(){
            return{
                base64:'',
            }
        },
        methods:{
            login:function () {
                axios({
                    url:'http://localhost:8976/ad/findbyid?id=1',
                    method:'get',
                    params: {
                        id:1
                    }
                }).then((res)=> {

                    this.base64 = 'data:image/jpeg;base64,'+res.data.base64picture;
                    console.log(this.base64);
                })

            }
        }
    }
</script>

