import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    aduser:{
      username:'',
      password:'',
      id:''
    },
    manager:{
      username:'',
      password:'',
      access_token:'',
    }
  },
  mutations: {
    adlogin(state,aduser) {
      state.aduser.password=aduser.username;
      state.aduser.username=aduser.password;
      state.aduser.id=aduser.id;
    },
    managerlogin(state,aduser) {
      state.manager.username = aduser.username;
      state.manager.password= aduser.password;
    },
    managetoken(state,token){
      state.manager.access_token = token;
    }

  },
  actions: {

  }
})
