<template>
    <div>
        n内容管理
        <Menu mode="horizontal" active-name="1">
            <MenuItem name="1">
                <Icon type="ios-paper" />
                <router-link to="/admanage">广告管理</router-link>
            </MenuItem>
            <MenuItem name="2">
                <Icon type="ios-people" />
                <router-link to="/usermanage">用户管理</router-link>
            </MenuItem>

        </Menu>
        <template>
            <Table ref="selection" size="large" stripe :columns="columns1" :data="userlist">
                <template slot-scope="{ row }" slot="entername">
                    <strong>{{ row.entername }}</strong>
                </template>
                <template slot-scope="{ row }" slot="title">
                    <p>{{ row.title }}</p>
                </template>
                <template slot-scope="{ row }" slot="detail">
                    <p>{{ row.detail }}</p>
                </template>
                <template slot-scope="{ row }" slot="uid">
                    <p>{{ row.uid }}</p>
                </template>
                <template slot-scope="{ row }" slot="base64picture">
<!--                    <p>{{ row.base64picture }}</p>-->
                    <img v-bind:src="row.base64picture" alt="" height="50px">
                </template>

                <template slot-scope="{ row }" slot="enable">
                    <p>{{ row.enable }}</p>
                </template>
                <template slot-scope="{ row, index }" slot="action">
                    <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">able</Button>
                    <Button type="error" size="small" @click="remove(index)">unable</Button>
                </template>
            </Table>
            <Button style="float:left" @click="handleSelectAll(true)">Set all selected</Button>
            <Button style="float:left" @click="handleSelectAll(false)">Cancel all selected</Button>
        </template>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "admanage",
        data () {
            return {
                columns1: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: 'Company Name',
                        slot: 'entername',
                    },
                    {
                        title: 'Title',
                        slot:'title',
                        sortable: true

                    },
                    {
                        title: 'Detail',
                        slot: 'detail'
                    },
                    {
                        title:'User ID',
                        slot:'uid'
                    },
                    {
                        title:'Phone',
                        slot:'base64picture'
                    },
                    {
                        title:'Enbale',
                        slot:'enable',
                        align: 'center'
                    },
                    {
                        title:'Action',
                        slot: 'action',
                        width: 150,
                        align: 'center'
                    }
                ],
                userlist:[]
            }
        },
        methods:{

        },
        created:function () {
            axios({
                url:'http://202.120.40.8:30459/ad/all',
                method:'get',
                // params:{
                //     username:'222',
                //     password:'1',
                //     email:'12'
                // }
                // headers:{
                //     "Authorization": "Bearer f76c6019-9ef3-464a-bea9-deb5b4514a81"
                // }
            }).then( (res) => {
                console.log('aa');
                console.log(res.data);
                for(var i=0;i<res.data.length;i++) {
                    this.userlist.push(res.data[i]);
                    this.userlist[i].base64picture='data:image/jpeg;base64,'+this.userlist[i].base64picture;
                }
                console.log(this);
            })
        },
    }
</script>

