<template>
    <div>
        <Strong style="font-size: 24px">广告商注册</Strong>
        <Divider />
        <div style="width: 20%;margin: auto">
            <Input type="text" v-model="formInline.username" placeholder="Username">
                <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div style="width: 20%;margin: auto;">
            <Input type="password" v-model="formInline.password" placeholder="Password">
                <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div style="width: 20%;margin: auto;">
            <Input type="password" v-model="formInline.companyname" placeholder="Company Name">
                <Icon type="ios-contacts" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div style="width: 20%;margin: auto;">
            <Input type="password" v-model="formInline.pricipal" placeholder="Principal">
                <Icon type="ios-contact" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div style="width: 20%;margin: auto;">
            <Input type="password" v-model="formInline.phone" placeholder="Phone">
                <Icon type="ios-phone-portrait" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div style="width: 20%;margin: auto;">
            <Input type="password" v-model="formInline.email" placeholder="Email">
                <Icon type="ios-mail" slot="prepend"></Icon>
            </Input>
        </div>
        <br>
        <div style="width: 20%;margin: auto;">
<!--            <p  style="display:inline;font-size: 14px;" >-->
<!--            <span style="display:inline-block;width:100px;text-align:right;">gender:</span>-->
<!--            </p>-->
            <RadioGroup v-model="formInline.gender" size="small">
                <Radio label="male">
                    <Icon type="ios-man"></Icon>
                    <span>male</span>
                </Radio>
                <Radio label="female">
                    <Icon type="ios-woman"></Icon>
                    <span>female</span>
                </Radio>
            </RadioGroup>
        </div>
        <br>
        <div>
            <Button type="primary" @click="register">Signin</Button>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name: "Aduserregister",
        data () {
            return {
                formInline: {
                    username: '',
                    password: '',
                    gender: 'male',
                    companyname:'',
                    pricipal:'',
                    phone:'',
                    email:'',
                },
            }
        },
        methods:{
            register:function () {
                console.log(this.formInline);
                axios({
                    url:'http://202.120.40.8:30459/aduser/add',
                    method:'get',
                    params: {
                        username:this.formInline.username,
                        password:this.formInline.password,
                        gender:this.formInline.gender,
                        companyname:this.formInline.companyname,
                        responsename: this.formInline.pricipal,
                        phone:this.formInline.phone,
                        email:this.formInline.email
                    }
                }).then((res)=>{
                    console.log(res);
                    if(res.data=="register success"){
                        this.$Message.success('注册成功');
                    }else {
                        this.$Message.error('用户名已存在，注册失败');
                    }
                })
            }
        }
    }
</script>

<style scoped>

